import {BaseOptionClass, BaseQuesClass} from "./BaseClass";
import Fun from "../Fun";

let inc = 0;
/**
 * 
 */
class MatrixSelectRowOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.varName = 'K_' + Fun.getInc();
    this.fieldName = this.varName;
    this.htmlGroup = '';
    this.noRandom = false;//
  }
}
class MatrixSelectColOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.varName = 'K_' + Fun.getInc();
    this.fieldName = this.varName;
    this.noRandom = false;//
  }
}
class MatrixSelectListOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.val = Fun.getValInc();
    this.noRandom = false;//
  }
}
/**
 * 
 */
class MatrixSelectClass extends BaseQuesClass {
  constructor(design = true) {
    super();
    this.quesType = 'MatrixSelect';
    this.htmlTitle = '<p><strong></strong></p>';
    this.random = false;//
    this.width = '';
    this.width1 = '';//

    this.pcWidth = '800px';
    this.pcWidth1 = '200px';

    this.phoneWidth = '100%';
    this.phoneWidth1 = '85px';

    this.list = [];//
    this.rows = [];
    this.cols = [];
    if (design) {//
      this.init();
    }
  }

  /**
   * 
   * @param an
   * @returns {boolean}
   */
  anValid(an) {
    super.anValid(an);
    if (!this.required) {
      return true
    }
    let sums = 0;//
    for (let row of this.rows) {
      for (let col of this.cols) {
        if (!Fun.isNull(an[row.varName + col.varName])) {
          sums++;
        }
      }
    }

    if (sums != this.rows.length * this.cols.length) {
      this.errInfo = ""
      return false
    }
    this.errInfo = ""
    return true
  }

  /**
   * 
   */
  anInit() {
    super.anInit();
    if (this.device == 'pc') {
      this.width = this.pcWidth;
      this.width1 = this.pcWidth1;
    } else {
      this.width = this.phoneWidth;
      this.width1 = this.phoneWidth1;
    }
  }

  /**
   * 
   */
  anRandom() {
    super.anRandom();
    if (this.random) {
      Fun.optionRandom(this.rows)
      Fun.optionRandom(this.cols)
      Fun.optionRandom(this.list)
    }
  }

  getVarNames() {
    let varSet = new Set();
    for (let row of this.rows) {
      for (let col of this.cols) {
        varSet.add(row.varName + col.varName)
      }
    }
    return varSet
  }

  init() {
    let labels = ['<p></p>', '<p></p>']
    let rate = null
    for (let label of labels) {
      rate = new MatrixSelectRowOptionClass();
      rate.htmlLabel = label
      this.rows.push(rate)
    }

    labels = ['<p></p>', '<p></p>', '<p></p>']
    for (let label of labels) {
      rate = new MatrixSelectColOptionClass();
      rate.htmlLabel = label
      this.cols.push(rate)
    }

    labels = ['', '', '', '', '']
    for (let label of labels) {
      rate = new MatrixSelectListOptionClass();
      rate.htmlLabel = label
      this.list.push(rate)
    }
  }

  addOption(index, flag) {
    if (flag == 'row') {
      let option = new MatrixSelectRowOptionClass();
      option.htmlLabel = "<p>" + (this.rows.length + 1) + "</p>";
      if (index == -1) {//
        this.rows.push(option)
      } else {//
        this.rows.splice(index + 1, 0, option);
      }
    } else if (flag == 'col') {
      let option = new MatrixSelectColOptionClass();
      option.htmlLabel = "<p>" + (this.cols.length + 1) + "</p>";
      if (index == -1) {//
        this.cols.push(option)
      } else {//
        this.cols.splice(index + 1, 0, option);
      }
    } else {
      let option = new MatrixSelectListOptionClass();
      option.htmlLabel = "<p>" + (this.list.length + 1) + "</p>";
      if (index == -1) {//
        this.list.push(option)
      } else {//
        this.list.splice(index + 1, 0, option);
      }
    }
  }

  delOption(index, flag) {
    if (flag == 'row') {
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
    } else if (flag == 'col') {
      if (this.cols.length > 1) {
        this.cols.splice(index, 1)
      }
    } else {
      if (this.list.length > 1) {
        this.list.splice(index, 1)
      }
    }

  }

  batchAddOptions(batchLabels, flag) {
    if (flag == 'row') {
      batchLabels.forEach(label => {
        let option = new MatrixSelectRowOptionClass();
        option.htmlLabel = label;
        this.rows.push(option)
      })
    } else if (flag == 'col') {
      batchLabels.forEach(label => {
        let option = new MatrixSelectColOptionClass();
        option.htmlLabel = label;
        this.cols.push(option)
      })
    } else {
      batchLabels.forEach(label => {
        let option = new MatrixSelectListOptionClass();
        option.htmlLabel = label;
        this.list.push(option)
      })
    }

  }

  /**
   * ,
   */
  getBatchLabels(flag) {
    if (flag == 'row') {
      let htmlLabels = []
      this.rows.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels;
    } else if (flag == 'col') {
      let htmlLabels = []
      this.cols.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels;
    } else {
      let htmlLabels = []
      this.list.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels;
    }

  }

  /**
   * 
   * @param index
   * @param flag
   */
  moveOption(index, optionFlag, flag = 'pre') {
    if (optionFlag == 'row') {
      let currOption = this.rows[index];
      if (flag === 'pre') {//
        this.rows.splice(index, 1);//
        this.rows.splice(index - 1, 0, currOption);//
      } else {
        this.rows.splice(index, 1);//
        this.rows.splice(index + 1, 0, currOption);//
      }
    } else if (optionFlag == 'col') {
      let currOption = this.cols[index];
      if (flag === 'pre') {//
        this.cols.splice(index, 1);//
        this.cols.splice(index - 1, 0, currOption);//
      } else {
        this.cols.splice(index, 1);//
        this.cols.splice(index + 1, 0, currOption);//
      }
    } else {
      let currOption = this.list[index];
      if (flag === 'pre') {//
        this.list.splice(index, 1);//
        this.list.splice(index - 1, 0, currOption);//
      } else {
        this.list.splice(index, 1);//
        this.list.splice(index + 1, 0, currOption);//
      }
    }

  }
}
export default MatrixSelectClass




