import {BaseQuesClass,BaseOptionClass} from './BaseClass'
import Fun from '../Fun'
let inc = 0 ;
  /**
   * 
   */
  class MulInputOptionClass extends BaseOptionClass {
    constructor() {
      super();
      this.varName = 'M_'+Fun.getInc();
      this.fieldName = this.varName;
      this.htmlGroup = '';
      this.rightLabel = '';//
      this.noRandom = false;//
      this.required = false;//
      this.rule = '';//
    }
  }
  /**
   * 
   */
  class MulInputClass extends BaseQuesClass {
    constructor(design=true) {
      super();
      this.quesType = 'MulInput';
      this.htmlTitle = '<p><strong></strong></p>';
      this.random = false;//
      this.min = 0;
      this.max = 0;
      this.width = '' ;
      this.width1 = '';//

      this.pcWidth = '45%' ;
      this.pcWidth1 = '15%' ;
      this.pcWidth2 = '85%' ;

      this.phoneWidth = '100%' ;
      this.phoneWidth1 = '25%' ;
      this.phoneWidth2 = '75%' ;
      this.rows = []
      if(design){//
        this.init();
      }
    }

    /**
     * 
     * @param an
     * @returns {boolean}
     */
    anValid(an) {
      super.anValid(an);
      if (!this.required) {
        return true
      }
      let sum = 0;//
      for (let item of this.rows) {
        if (Fun.isNull(an[item.varName])) {
          if (item.required) {
            this.errInfo = "*"
            return false;
          }
        } else {//
          if(!Fun.isNull(item.rule)){//
            let tip = Fun.validRule(item.rule,an[item.varName])
            if(!Fun.isNull(tip)){
              this.errInfo = tip
              return false
            }
          }
          sum++
        }
      }
      if (sum == 0) {
        this.errInfo = ""
        return false
      }
      if (this.anHasOptionSum()) {//
        if (this.min > sum || this.max < sum) {
          this.errInfo = ""
          return false;
        }
      }
      this.errInfo = ""
      return true;
    }
    /**
     * 
     */
    anInit(){
      super.anInit();
      if (this.device == 'pc') {
        this.width = this.pcWidth ;
        this.width1 = this.pcWidth1 ;
        this.width2 = this.pcWidth2 ;
      }else{
        this.width = this.phoneWidth ;
        this.width1 = this.phoneWidth1 ;
        this.width2 = this.phoneWidth2 ;
      }
    }
    getVarNames(){
      let varSet = new Set();
      for (let item of this.rows) {
        varSet.add(item.varName)
      }
      return varSet
    }
    /**
     * 
     * @returns {boolean}
     */
    anHasOptionSum(){
      if (this.max != 0 && (this.min <= this.max)) {
        if (this.max <= this.rows.length) {
          return true;
        }
      }
      return false;
    }
    anOptionSumInfo(){
      let start = this.min > 0 ? '' + this.min + '-' : ''
      return '[' + start + this.max + ']'
    }

    /**
     * 
     */
    anRandom(){
      super.anRandom();
      if (this.random) {
        Fun.optionRandom(this.rows)
      }
    }

    init(){
      let labels = ['<p></p>','<p></p>']
      let rate = null
      for(let label of labels){
        rate = new MulInputOptionClass();
        rate.htmlLabel = label
        this.rows.push(rate)
      }
    }
    addOption(index) {
      let option = new MulInputOptionClass();
      option.htmlLabel = "<p>" + (this.rows.length+1)+"</p>";
      if (index == -1) {//
        this.rows.push(option)
      } else {//
        this.rows.splice(index + 1, 0, option);
      }
    }
    delOption(index){
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
    }
    batchAddOptions(batchLabels){
      batchLabels.forEach(label => {
        let option = new MulInputOptionClass();
        option.htmlLabel = label;
        this.rows.push(option)
      })
    }


    /**
     * 
     * @param index
     * @param flag
     */
    moveOption(index,flag='pre'){
      let currOption = this.rows[index];
      if(flag==='pre'){//
        this.rows.splice(index, 1);//
        this.rows.splice(index - 1, 0, currOption);//
      }else{
        this.rows.splice(index, 1);//
        this.rows.splice(index + 1, 0, currOption);//
      }
    }
  }
  export default MulInputClass



