import Fun from "../Fun.js";
import EL from "../EL.js";
/**
 * 
 */
class BaseOptionClass {
  constructor() {
    this.htmlLabel = '';
  }
}
/**
 * 
 */
class BaseQuesClass {
  constructor() {
    this._id='';
    this.quesId = '';
    this.surveyId = '';
    this.errInfo = '';//
    this.quesType = 'Input';
    this.htmlTitle = '';
    this.descFlag = false;//
    this.htmlDesc = '';//
    this.tipsFlag = false;
    this.htmlTips = '';
    this.jumpEl = null;
    this.jumpPage = null;//
    this.jumpEndVal = 'A'
    this.durationFlag = false;//
    this.duration = 0;
    this.required = true;//
    this.requiredShow = true;//

    this.device = 'phone';

    this.style = '';
    this.pcStyle = '';
    this.phoneStyle = '';

    this.paddingLeft = '';//
    this.pcPaddingLeft = '';
    this.phonePaddingLeft = '';

    //this.pcStyles = [{code: 'default', label: ''}];
    //this.mobileStyles = [{code: 'default', label: ''}];
    this.visibleEl = null;
  }

  anInit() {
    if (this.device == 'pc') {
      this.style = this.pcStyle;
      this.paddingLeft = this.pcPaddingLeft
    } else {
      this.style = this.phoneStyle;
      this.paddingLeft = this.phonePaddingLeft
    }
  }

  /**
   * 
   * @param an
   * @returns {boolean}
   */
  anValid(an) {
    return true
  }

  anRandom(){
  }

  getVarNames() {
    return []
  }

  /**
   * 
   * @returns {boolean}
   */
  anHasOptionSum() {
    return false;
  }

  /**
   * 
   * @returns {}
   */
  getVisibleVarNames() {
    return this.getElVarNames('visibleEl')
  }

  /**
   * 
   */
  getElVarNames(elType) {
    let el = null;
    if (elType === 'visibleEl') {//
      el = this.visibleEl;
    } else if (elType === 'jumpEl') {
      el = this.jumpEl;
    }
    //console.log(this.quesId)
    let varSet = new Set();
    if (Fun.isNull(el)) {//
      return varSet;//
    } else {
      this.callElVarNames(el, varSet);
    }
    return varSet;//
  }

  /**
   * 
   * @param el
   * @param varSet
   */
  callElVarNames(el, varSet) {
    if (!Fun.isNull(el.type)) {
      if ((el.type === '||' || el.type === '&&') && !Fun.isNull(el.childs)) {
        for (let currEl of el.childs) {
          this.callElVarNames(currEl, varSet);
        }
      } else {//type=el
        if (!Fun.isNull(el.varName)) {
          varSet.add(el.varName);
        }
      }
    }
  }

  anDefaultVal(ue,an) {

  }

  getVisible(an, varNameSet) {
    let visible = true
    if (this.hasVisibleEl()) {
      let elTree = new EL.ElTree()
      elTree.buildByFilter(this.visibleEl)
      elTree.tree2Arr()
      visible = elTree.elLogicVal(an, varNameSet)
    }
    if (!visible) {
      this.removeNoVisibleAn(an)
    }
    return visible;
  }
  /**
   * 
   * @param an
   */
  removeNoVisibleAn(an){
    let varSet = this.getVarNames()
    for(let varName of varSet){
      if(!Fun.isNull(an[varName])){
        an[varName] = undefined ;
      }
    }
  }

  hasVisibleEl() {
    if (!Fun.isNull(this.visibleEl)) {
      return true
    } else {
      return false
    }
  }

  hasJumpEl() {
    if (!Fun.isNull(this.jumpEl)) {
      return true
    } else {
      return false
    }
  }
}
export{
  BaseOptionClass,
  BaseQuesClass,
}



