  import {BaseQuesClass} from './BaseClass'

  /**
   * 
   */
  class StartClass extends BaseQuesClass {
    constructor(design = true) {
      super();
      this.quesType = 'Start';
      this.htmlTitle = '<p></p>';
      this.required = false;//
      this.use = true ;
      this.content = '';
      this.bgColor='' ;//

      this.width = '' ;
      this.width1 = '';//

      this.pcWidth = '100%' ;
      this.pcWidth1 = '20%' ;

      this.phoneWidth = '100%' ;
      this.phoneWidth1 = '100%' ;

      this.btTextAlign = 'center';//
      this.pcBtTextAlign = 'center';
      this.phoneBtTextAlign = 'center';

    }

    /**
     * 
     */
    anInit(){
      super.anInit();
      if (this.device == 'pc') {
        this.width = this.pcWidth ;
        this.width1 = this.pcWidth1 ;
        this.btTextAlign = this.pcBtTextAlign ;
      }else{
        this.width = this.phoneWidth ;
        this.width1 = this.phoneWidth1 ;
        this.btTextAlign = this.phoneBtTextAlign ;
      }
    }
  }
  export default StartClass




