import {BaseOptionClass, BaseQuesClass} from "./BaseClass";
import Fun from "../Fun";

/**
 * 
 */
class MatrixInputRowOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.varName = 'F_'+Fun.getInc();
    this.fieldName =  this.varName;
    this.htmlGroup = '';
    this.rule = '';//
    this.noRandom = false;//
  }
}
class MatrixInputColOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.varName = 'F_'+Fun.getInc();
    this.fieldName = this.varName;
    this.noRandom = false;//
  }
}
/**
 * 
 */
class MatrixInputClass extends BaseQuesClass {
  constructor(design=true) {
    super();
    this.quesType = 'MatrixInput';
    this.htmlTitle = '<p><strong></strong></p>';
    this.random = false;//
    this.optionSumShow = true ;//
    this.min = 0;
    this.max = 0;
    this.width = '' ;
    this.pcWidth = '45%' ;
    this.phoneWidth = '100%' ;
    this.rows = [];
    this.cols = [];
    if(design){//
      this.init();
    }
  }

  /**
   * 
   * @returns {boolean}
   */
  anHasOptionSum(){
    if (this.max != 0 && (this.min <= this.max)) {
      if (this.max <= this.cols.length) {
        return true;
      }
    }
    return false;
  }
  anOptionSumInfo(){
    let start = this.min > 0 ? '' + this.min+ '-' : ''
    return '[' + start + this.max + ']'
  }
  /**
   * 
   * @param an
   * @returns {boolean}
   */
  anValid(an) {
    if (!this.required) {
      return true
    }
    let sums = [];//
    for (let i = 0; i < this.rows.length; i++) {
      let row = this.rows[i]
      sums[i] = 0;
      for (let col of this.cols) {
        if (!Fun.isNull(an[row.varName + col.varName])) {
          sums[i]++;
        }
      }
    }
    for (let sum of sums) {
      if (sum == 0) {
        this.errInfo = ""
        return false
      }
    }

    if (this.anHasOptionSum()) {//
      for (let sum of sums) {
        if (this.min > sum || this.max < sum) {
          this.errInfo = ""
          return false;
        }
      }
    }
    this.errInfo = ""
    return true
  }
  /**
   * 
   */
  anInit(){
    super.anInit();
    if(this.device=='pc'){
      this.width = this.pcWidth ;
    }else{
      this.width = this.phoneWidth ;
    }
  }
  /**
   * 
   */
  anRandom(){
    super.anRandom();
    if (this.random) {
      Fun.optionRandom(this.rows)
      Fun.optionRandom(this.cols)
    }
  }
  init(){
    let labels = ['<p></p>','<p></p>']
    let rate = null
    for(let label of labels){
      rate = new MatrixInputRowOptionClass();
      rate.htmlLabel = label
      this.rows.push(rate)
    }

    labels = ['<p></p>','<p></p>','<p></p>']
    for(let label of labels){
      rate = new MatrixInputColOptionClass();
      rate.htmlLabel = label
      this.cols.push(rate)
    }
  }

  getVarNames(){
    let varSet = new Set();
    for (let row of this.rows) {
      for (let col of this.cols) {
        varSet.add(row.varName+col.varName)
      }
    }
    return varSet
  }

  addOption(index,flag) {
    if(flag=='row'){
      let option = new MatrixInputRowOptionClass();
      option.htmlLabel = "<p>" + (this.rows.length + 1)+"</p>";
      if (index == -1) {//
        this.rows.push(option)
      } else {//
        this.rows.splice(index + 1, 0, option);
      }
    }else{
      let option = new MatrixInputColOptionClass();
      option.htmlLabel = "<p>" + (this.cols.length + 1)+"</p>";
      if (index == -1) {//
        this.cols.push(option)
      } else {//
        this.cols.splice(index + 1, 0, option);
      }
    }


  }

  delOption(index,flag) {
    if(flag=='row'){
      if (this.rows.length > 1) {
        this.rows.splice(index, 1)
      }
    }else{
      if (this.cols.length > 1) {
        this.cols.splice(index, 1)
      }
    }

  }

  batchAddOptions(batchLabels,flag) {
    if(flag=='row'){
      batchLabels.forEach(label => {
        let option = new MatrixInputRowOptionClass();
        option.htmlLabel = label;
        this.rows.push(option)
      })
    }else{
      batchLabels.forEach(label => {
        let option = new MatrixInputColOptionClass();
        option.htmlLabel = label;
        this.cols.push(option)
      })
    }

  }

  /**
   * ,
   */
  getBatchLabels(flag) {
    if(flag=='row'){
      let htmlLabels = []
      this.rows.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels;
    }else{
      let htmlLabels = []
      this.cols.forEach((option) => {
        let htmlLabel = option.htmlLabel;
        if (!Fun.isNull(htmlLabel)) {
          htmlLabels.push(htmlLabel)
        }
      })
      return htmlLabels;
    }

  }

  /**
   * 
   * @param index
   * @param flag
   */
  moveOption(index,optionFlag, flag = 'pre') {
    if(optionFlag=='row'){
      let currOption = this.rows[index];
      if (flag === 'pre') {//
        this.rows.splice(index, 1);//
        this.rows.splice(index - 1, 0, currOption);//
      } else {
        this.rows.splice(index, 1);//
        this.rows.splice(index + 1, 0, currOption);//
      }
    }else{
      let currOption = this.cols[index];
      if (flag === 'pre') {//
        this.cols.splice(index, 1);//
        this.cols.splice(index - 1, 0, currOption);//
      } else {
        this.cols.splice(index, 1);//
        this.cols.splice(index + 1, 0, currOption);//
      }
    }

  }
}
export default MatrixInputClass




