import Fun from "./Fun.js";
import SERVICE2 from "../config/service2.js";

let WXjssdk = {//
  version: "1.0",//
  appName: "kukaSurvey", //
  isReady: false,//JS SDK
  access_token: "",//
  ticket: "",//
  config: {
    debug: false, // ,apialertpclogpc
    appId: 'wxf63021da44864cf1', // 
    timestamp: Math.ceil(new Date().getTime() / 1000).toString(), // 
    nonceStr: 'html5kuka_wxJSSDK', // 
    signature: '',// 1
    jsApiList: [
      "onMenuShareAppMessage",
      "onMenuShareTimeline",
      'hideMenuItems',
      "getLocation",
      "openLocation"
    ] // JSJS2
  },
  /*
   
   */
  init: function () {
    if(!Fun.isWeiXin()){//
      return;
    }
    if (!wx) {//js
      alert("js");
      return;
    }
    let that = this;//
    //
    this.wx_get_token(function (data) {
      //alert("a1="+data.access_token)
      if (data.access_token) {
        Cookie.Set("access_token", data.access_token, 3600);
        that.access_token = data.access_token;
      }
      //
      that.wx_get_ticket(function (data) {
        if (data.ticket) {
          Cookie.Set("ticket", data.ticket, 3600);
          that.ticket = data.ticket
        }
        //
        that.wx_get_signature(function (data) {
          that.config.signature = data;
          that.initWx(function () {//
            //
          });
        });
      });
    });
  },
  //
  wx_get_token: function (call) {
    this.access_token = Cookie.Get("access_token");
    if (!Cookie.Get("access_token")) {
      SERVICE2({url: 'wx/getAccessToken'}).then(res => {
        call && call(res.data.res);
      })
    } else {
      call && call({});
    }
  },
  //
  wx_get_ticket: function (call) {
    this.ticket = Cookie.Get("ticket");
    if (!this.ticket) {
      let _reqObj = {
        url: 'wx/getJsapiTicket',
        params: {access_token: this.access_token,}
      }
      SERVICE2(_reqObj).then(res => {
        call && call(res.data.res);
      })
    } else {
      call && call({});
    }

  },
  //
  wx_get_signature: function (call) {
    let _reqObj = {
      url: 'wx/getSignature',
      params: {
        jsapi_ticket: this.ticket, // 
        timestamp: this.config.timestamp, // 
        noncestr: this.config.nonceStr, // 
        url: window.location.href // 
      }
    }
    SERVICE2(_reqObj).then(res => {
      //alert("signature="+res.data.res.signature)
      call && call(res.data.res.signature);
    })

  },
  initWx: function (call, errorCall) {//
    let that = this;
    wx.config(this.config);//
    /*configreadyconfig
     *config
     *ready
     *ready
     * */

    wx.ready(function () {
      //  API
        let title = "-",
        link = "http://www.kukababy.com",
        imgUrl = "http://www.kukababy.com/resource/img/logo.png",
        desc = "-",
        success = function () {
          //alert("!");
        },
        cancel = function () {
          //alert("!");
        };
      // wx.hideMenuItems({
      //   menuList: ['menuItem:share:appMessage'] // menu3
      // });
      wx.onMenuShareTimeline({//
        //title: title, // 
        //link: link, // 
        imgUrl: imgUrl, // 
        success: function () {
          success();
        },
        cancel: function () {
          cancel();
        }
      });
      wx.onMenuShareAppMessage({
        title: title, // 
        desc: SYSCFG.wx.wxDesc, // 
        //link: link, // 
        imgUrl: imgUrl, // 
        type: "link", // ,musicvideolinklink
        dataUrl: "", // typemusicvideo
        success: function () {
          success();
        },
        cancel: function () {
          cancel();
        }
      });
      let latitude, longitude, speed, accuracy;
      // wx.getLocation({
      //   success: function (res) {
      //     latitude = res.latitude; 		// 90 ~ -90
      //     longitude = res.longitude; 	// 180 ~ -180
      //     speed = res.speed; 		    // /
      //     accuracy = res.accuracy; 	    // 
      //     //$("#accuracy").html(accuracy);
      //     let info = latitude+" "+longitude+" "+speed+" "+accuracy
      //     alert(info)
      //   }
      // });

      //$("#openLocation").click(function () {	//
      //   wx.openLocation({
      //     latitude: latitude, 		// 90 ~ -90
      //     longitude: longitude, 	// 180 ~ -180
      //     name: '', 		// 
      //     address: '', 	// 
      //     scale: 1, 			// ,,1~28
      //     infoUrl: 'http://www.baidu.com' // 
      //   });
      //});
      that.isReady = true;
      call && call();
    });
    /*configerror
     *configdebugres
     * SPA
     * */
    wx.error(function (res) {
      that.isReady = "false";
      errorCall && errorCall();
    });

  }
};
//
WXjssdk.init();


