import {BaseOptionClass, BaseQuesClass} from "./BaseClass";
import Fun from "../Fun";

/**
 * 
 */
class CheckboxOptionClass extends BaseOptionClass {
  constructor() {
    super();
    this.varName = 'A_'+Fun.getInc();
    this.varNameT = 'T'+this.varName;
    this.fieldName = this.varName;
    this.fieldNameT = this.varNameT;
    this.htmlGroup = '';
    this.fillFlag = false; //
    this.fillRequired = false;//
    this.dft = false;//
    this.noRandom = false;//
    this.mutex = false;//
    this.imgUrl = Fun.getLogoUrl();
  }
}
/**
 * 
 */
class CheckboxClass extends BaseQuesClass {
  constructor(design = true) {
    super();
    this.quesType = 'Checkbox';
    this.htmlTitle = '<p><strong></strong></p>';
    this.random = false;//
    this.optionSumShow = true ;//
    this.min = 0;//
    this.max = 0;
    this.width = '' ;
    this.width1 = '';//
    this.horCol = '1';//

    this.pcHorCol = '1' ;
    this.phoneHorCol = '1' ;

    this.imgWidth = '' ;//
    this.imgHeight = '';//
    this.imgShape = '';

    this.pcImgShape = 'img-rounded' ;
    this.phoneImgShape = 'img-rounded' ;

    this.pcImgWidth = '150px' ;
    this.pcImgHeight = '150px' ;

    this.phoneImgWidth = '110px' ;
    this.phoneImgHeight = '110px' ;

    this.pcWidth = '95%' ;
    this.pcWidth1 = '95%' ;

    this.phoneWidth = '100%' ;
    this.phoneWidth1 = '100%' ;

    this.rows = []
    if (design) {//
      this.init();
    }
  }

  /**
   * 
   * @param an
   * @returns {boolean}
   */
  anValid(an) {
    if (!this.required) {
      return
    }
    let sum = 0;//
    for (let item of this.rows) {
      if (!Fun.isNull(an[item.varName])) {
        sum++;
      }
    }
    if (sum == 0) {
      this.errInfo = ""
      return false;
    } else {
      for (let item of this.rows) {
        if (item.fillFlag && item.fillRequired) {
          if (!Fun.isNull(an[item.varName]) && Fun.isNull(an[item.varNameT])) {
            this.errInfo = "*"
            return false;
          }
        }
      }
    }
    if (this.anHasOptionSum()) {//
      if (this.min > sum || this.max < sum) {
        this.errInfo = ""
        return false;
      }
    }
    this.errInfo = ""
    return true;
  }

  /**
   * 
   */
  anInit() {
    super.anInit();
    if (this.device == 'pc') {
      this.width = this.pcWidth;
      this.width1 = this.pcWidth1;
      this.horCol = this.pcHorCol;
      this.imgWidth = this.pcImgWidth ;
      this.imgHeight = this.pcImgHeight ;
      this.imgShape = this.pcImgShape ;
    } else {
      this.width = this.phoneWidth;
      this.width1 = this.phoneWidth1;
      this.horCol = this.phoneHorCol;
      this.imgWidth = this.phoneImgWidth ;
      this.imgHeight = this.phoneImgHeight ;
      this.imgShape = this.phoneImgShape ;
    }
  }

  getVarNames(){
    let varSet = new Set();
    for (let item of this.rows) {
      varSet.add(item.varName)
      varSet.add(item.varNameT)
    }
    return varSet
  }


  /**
   * 
   */
  anRandom(){
    super.anRandom();
    if (this.random) {
      Fun.optionRandom(this.rows)
    }
  }

  /**
   * 
   * @returns {boolean}
   */
  anHasOptionSum() {
    if (this.max != 0 && (this.min <= this.max)) {
      if (this.max <= this.rows.length) {
        return true;
      }
    }
    return false;
  }

  anOptionSumInfo() {
    let start = this.min > 0 ? '' + this.min + '-' : ''
    return '[' + start + this.max + ']'
  }


  init() {
    let labels = ['<p>1</p>', '<p>2</p>']
    let rate = null
    for (let label of labels) {
      rate = new CheckboxOptionClass();
      rate.htmlLabel = label
      this.rows.push(rate)
    }
  }

  addOption(index) {
    let option = new CheckboxOptionClass();
    option.htmlLabel = "<p>" + (this.rows.length + 1)+"</p>";
    if (index == -1) {//
      this.rows.push(option)
    } else {//
      this.rows.splice(index + 1, 0, option);
    }
  }

  delOption(index) {
    if (this.rows.length > 1) {
      this.rows.splice(index, 1)
    }
  }

  batchAddOptions(batchLabels) {
    batchLabels.forEach(label => {
      let option = new CheckboxOptionClass();
      option.htmlLabel = label;
      this.rows.push(option)
    })
  }

  /**
   * ,
   */
  getBatchLabels() {
    let htmlLabels = []
    this.rows.forEach((option) => {
      let htmlLabel = option.htmlLabel;
      if (!Fun.isNull(htmlLabel)) {
        htmlLabels.push(htmlLabel)
      }
    })
    return htmlLabels;
  }

  /**
   * 
   * @param index
   * @param flag
   */
  moveOption(index, flag = 'pre') {
    let currOption = this.rows[index];
    if (flag === 'pre') {//
      this.rows.splice(index, 1);//
      this.rows.splice(index - 1, 0, currOption);//
    } else {
      this.rows.splice(index, 1);//
      this.rows.splice(index + 1, 0, currOption);//
    }
  }
}
export default CheckboxClass



